import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { getStaffCategoryById, updateStaffCategory, createStaffCategory } from 'services/staffCategory';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    noOfFloor: 20,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormStaffCategoryPage' })
class FormStaffCategoryPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getStaffCategoryById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createStaffCategory({...values, ...{ categoryId: 1 }});
      } else {
        await updateStaffCategory({ id: dataId, ...values, ...{ categoryId: 1 }});
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} chức vụ`}>
        <Row>
          <Form onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStaffCategoryPage {...props} />
  </Page>
));



