import { request } from 'utils';

const prefix = '/api/organization';
// request.prefix = prefix;

export const getStaffCategories = ({
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Staff/StaffCategory',
    method: 'GET',
    params: { categoryId:1, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getStaffCategoryById = id =>
request({
  prefix,
  url: `/Staff/StaffCategory/${id}`,
  method: 'GET',
});

export const updateStaffCategory = ({ id, label, categoryId }) =>
  request({
    prefix,
    url: `/Staff/StaffCategory`,
    data: { id, label, categoryId },
    method: 'PUT',
  });

export const createStaffCategory = ({ id, label, categoryId }) =>
  request({
    prefix,
    url: `/Staff/StaffCategory`,
    data: { id, label, categoryId },
    method: 'POST',
  });

export const deleteStaffCategory = id =>
  request({
    prefix,
    url: `/Staff/StaffCategory/${id}`,
    method: 'DELETE',
  });


